body {
  font: 2vw;
  font-family: Arial, Helvetica, sans-serif;
  color: antiquewhite;
  margin: 20px;
  background-color: rgba(0, 0, 0, 0.87);
  display: flex;
  justify-content: center;
}

ol, ul {
  padding-left: 30px;
}

.game-board {
  box-shadow: 0vw 0.55vw 1vw 0.15vw rgb(0, 0, 0);
  padding: 2vw;
}

.board-row {
  margin-top: -0.1vw;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-top: -0.7vw;
  padding: 1.5vw;
  text-align: center;
  font-size: 2vw;
  font-family: chilanka, uroob, purisa, 'monotype corsiva' ;
  font-weight: 520;
}

.status .tooltiptext{
  visibility: hidden;
  background-color: rgba(46, 42, 42, 0.87);
  box-shadow: 0vw 0.55vw 1vw 0.15vw rgb(0, 0, 0);
  color: antiquewhite;
  text-align: center;
  border-radius: 6px;
  padding: 0.5vw;
  font-size: 1.3vw;

  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  margin-top: -2.8%;
  left: 49%;
}
.status:hover .tooltiptext {
  visibility: visible;
}

.square {
  border-radius: 0.5vw;
  background-color: burlywood;
  font-size: 2vw;
  font-weight: bold;
  line-height: 5vw;
  height: 5vw;
  margin: 0.2vw;
  padding: 1vw;
  text-align: center;
  width: 5vw;

}

.square:focus {
  outline: inset;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
